export interface DocumentModel {
  success: boolean
  message: string
  data: DocumentData
  errors: any[]
}

export interface DocumentData {
  totalCount: number
  page: number
  pageSize: number
  items: DocumentItem[]
}

export interface DocumentItem {
  id: number
  title: string
  type: number // 0: Letter, 1: Task, 2: Meeting, 3: Minute, 4: Contract, 5: Report, 6: Image, 7: Scan, 8: Other
  documentSource: number // 0: Internal, 1: External
  storagePath: string
  description: string
  createdByUserId: string
  createdByName: string
  createdAt: string
  updatedAt: string | null
  fileName: string
  fileExtension: string
  fileSize: number
}

export interface CreateDocumentDto {
  title: string
  type: number
  documentSource: number
  description: string
  file: File
}

export interface UpdateDocumentDto {
  title: string
  type: number
  documentSource: number
  description: string
}

export interface DocumentSearchParams {
  search?: string
  type?: number
  documentSource?: number
  from?: string
  to?: string
  pageSize?: number
  pageNumber?: number
}

// Enums for better type safety
export enum DocumentType {
  Letter = 0,
  Task = 1,
  Meeting = 2,
  Minute = 3,
  Contract = 4,
  Report = 5,
  Image = 6,
  Scan = 7,
  Other = 8
}

export enum DocumentSource {
  Internal = 0,
  External = 1
}
