import { Component, EventEmitter } from '@angular/core';
import { TranslationService } from '../../../services/translation.service';

export interface ConfirmationData {
  title: string;
  message: string;
  confirmText: string;
  cancelText: string;
  type: 'info' | 'warning' | 'delete' | 'success';
}

@Component({
  selector: 'app-documents-confirmation-modal',
  standalone: false,
  templateUrl: './documents-confirmation-modal.component.html',
  styleUrl: './documents-confirmation-modal.component.css'
})
export class DocumentsConfirmationModalComponent {
  showModal = false;
  data: ConfirmationData = {
    title: '',
    message: '',
    confirmText: '',
    cancelText: '',
    type: 'info'
  };

  confirm = new EventEmitter<void>();
  cancel = new EventEmitter<void>();

  constructor(private translationService: TranslationService) {}

  isRTL(): boolean {
    return this.translationService.isRTL();
  }

  show(confirmationData: ConfirmationData) {
    this.data = confirmationData;
    this.showModal = true;
  }

  hide() {
    this.showModal = false;
  }

  onConfirm() {
    this.confirm.emit();
    this.hide();
  }

  onCancel() {
    this.cancel.emit();
    this.hide();
  }

  getModalClass(): string {
    switch (this.data.type) {
      case 'delete':
        return 'modal-danger';
      case 'warning':
        return 'modal-warning';
      case 'success':
        return 'modal-success';
      default:
        return 'modal-info';
    }
  }

  getIconClass(): string {
    switch (this.data.type) {
      case 'delete':
        return 'fas fa-exclamation-triangle text-danger';
      case 'warning':
        return 'fas fa-exclamation-triangle text-warning';
      case 'success':
        return 'fas fa-check-circle text-success';
      default:
        return 'fas fa-info-circle text-info';
    }
  }

  getConfirmButtonClass(): string {
    switch (this.data.type) {
      case 'delete':
        return 'btn-danger';
      case 'warning':
        return 'btn-warning';
      case 'success':
        return 'btn-success';
      default:
        return 'btn-primary';
    }
  }
}
