import { HttpClient } from '@angular/common/http';
import { Injectable } from '@angular/core';
import { environment } from '../../environments/environment';
import { Observable } from 'rxjs';
import { 
  DocumentModel, 
  DocumentItem, 
  CreateDocumentDto, 
  UpdateDocumentDto, 
  DocumentSearchParams
} from '../models/document.model';

@Injectable({
  providedIn: 'root'
})
export class DocumentsService {
  
  public apiUrl = `${environment.apiBaseUrl}/Documents`;
  
  constructor(private http: HttpClient) { }

  getAllDocuments(params: DocumentSearchParams = {}): Observable<DocumentModel> {
    let url = this.apiUrl;
    const queryParams = new URLSearchParams();
    
    if (params.search) queryParams.append('Search', params.search);
    if (params.type !== undefined) queryParams.append('Type', params.type.toString());
    if (params.documentSource !== undefined) queryParams.append('DocumentSource', params.documentSource.toString());
    if (params.from) queryParams.append('From', params.from);
    if (params.to) queryParams.append('To', params.to);
    if (params.pageSize) queryParams.append('PageSize', params.pageSize.toString());
    if (params.pageNumber) queryParams.append('PageNumber', params.pageNumber.toString());
    
    if (queryParams.toString()) {
      url += '?' + queryParams.toString();
    }
    
    return this.http.get<DocumentModel>(url);
  }

  getDocument(id: number): Observable<{ success: boolean; message: string; data: DocumentItem; errors: any[] }> {
    return this.http.get<{ success: boolean; message: string; data: DocumentItem; errors: any[] }>(`${this.apiUrl}/${id}`);
  }

  createDocument(document: CreateDocumentDto): Observable<any> {
    const formData = new FormData();
    formData.append('Title', document.title);
    formData.append('Type', document.type.toString());
    formData.append('DocumentSource', document.documentSource.toString());
    formData.append('Description', document.description);
    formData.append('File', document.file);

    return this.http.post<any>(this.apiUrl, formData);
  }

  updateDocument(id: number, document: UpdateDocumentDto): Observable<any> {
    // Try sending data directly without updateDto wrapper
    const requestBody = {
      Title: document.title || '',
      Type: document.type,
      DocumentSource: document.documentSource,
      Description: document.description || ''
    };
    
    console.log('Service - Document data received:', document);
    console.log('Service - Request body being sent (direct):', requestBody);
    console.log('Service - Title value:', document.title);
    console.log('Service - Title type:', typeof document.title);
    console.log('Service - Title length:', document.title ? document.title.length : 'undefined');
    console.log('Service - JSON stringified:', JSON.stringify(requestBody, null, 2));
    
    return this.http.put<any>(`${this.apiUrl}/${id}`, requestBody, {
      headers: {
        'Content-Type': 'application/json'
      }
    });
  }

  deleteDocument(id: number): Observable<any> {
    return this.http.delete<any>(`${this.apiUrl}/${id}`);
  }

  downloadDocument(id: number): Observable<Blob> {
    return this.http.get(`${this.apiUrl}/${id}/download`, { responseType: 'blob' });
  }
}
